import React from 'react'
import { View, useWindowDimensions } from 'react-native'
import { COLORS, SMALL_SCREEN_THRESHOLD, LARGE_SCREEN_THRESHOLD, SPACING } from '../constants'
import RenderClient from '../components/list/RenderClient'
import { FlashList } from "@shopify/flash-list"

import { MOCK_DATA } from '../constants'

const Pri = ({ navigation, route }) => {
    const { width } = useWindowDimensions()
    const isSmalScreen = width < SMALL_SCREEN_THRESHOLD
    const isLargeScreen = width >= LARGE_SCREEN_THRESHOLD
    
    return (
        <View style={{ flex: 1, backgroundColor: COLORS.lightBlack, paddingVertical: SPACING.large }}>
            <FlashList
                data={MOCK_DATA}
                renderItem={({ item }) => <RenderClient client={item} />}
                estimatedItemSize={200}
                numColumns={isSmalScreen ? 2 : isLargeScreen ? 5 : 3 }
                contentContainerStyle={{ paddingHorizontal: SPACING.medium }}
            //ListEmptyComponent={{}}
            />
        </View>
    )
}

export default Pri